* {margin: 0; padding: 0; box-sizing: border-box;}
ul, ol, li {list-style: none; color: #333;}
a {text-decoration: none; color: #333;}

.App {
  text-align: center;
}

.slider {
  width: 100%;
//   height: 500px;
//   background-image: url('../public/img/slider.jpg');
//   background-size: cover;
//   background-position: center;
  img {
    width: 100%;
  }
}

.c1 {text-decoration: none; color:#333; text-align: center;}

.c1:hover {color: red;}

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: 0.5s;
}

.before {
  transform: scale(0);
}

.after {
  transform: scale(1);
  transition: 0.5s;
}

.about_wrap {
  height: 1000px;
  padding-top: 200px;


  .scroll-text {    
    font-size: 5rem;
    transition: 1.5s;
  }

  .scrolled-text {
    font-size: 5rem;
    color: red;
    transition: 1.5s;
  }
}
